import styled from 'styled-components';

import { COLORS, BREAKPOINTS } from '../styleguide';

const Container = styled.div`
  width: 100%;

  @media (min-width: ${BREAKPOINTS.tablet}px) {
    width: 700px;
    margin: auto;    
  }
`;

export default Container;


